@import "https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap";
@font-face {
  font-family: Cera Pro;
  src: local(Cera Pro Bold), local(CeraPro-Bold), url("CeraPro-Bold.aef3d36a.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}

:root {
  --rcot-color-yellow: #f7e64d;
  --rcot-color-pink: #ffc2ff;
  --rcot-color-white: #fff;
  --rcot-color-blue: #7ee1e8;
  --rcot-color-purple: #6f35a3;
  --rcot-color-teal: #43cfb5;
  --rcot-color-light-grey: #f0f0f0;
  --rcot-color-lilac: #7575df;
  --rcot-color-red: #e8705e;
  --rcot-color-dark-teal: #003643;
  --rcot-color-dark-grey: #616161;
  --spacing-small: 8px;
  --spacing-medium: 16px;
  --spacing-large: 32px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  max-width: 600px;
  padding: var(--spacing-large);
  background-color: var(--rcot-color-light-grey);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: Nunito Sans, sans-serif;
  display: flex;
}

form {
  gap: var(--spacing-medium);
  width: 100%;
  max-width: 600px;
  padding: var(--spacing-large);
  background-color: var(--rcot-color-white);
  margin-bottom: var(--spacing-medium);
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 4px 10px #0000001a;
}

.input {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

.form-item {
  color: var(--rcot-color-purple);
  text-align: center;
  font-size: 1rem;
}

input[type="text"] {
  height: calc(1.5em + .75rem + 2px);
  color: var(--rcot-color-dark-grey);
  border: 2px solid #00364380;
  border-radius: 4px;
  width: 100%;
  max-width: 500px;
  margin: auto;
  padding: .5rem;
  font-family: inherit;
  font-size: 1rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

input[type="text"]:hover, input[type="text"]:focus, input[type="text"]:active {
  border-color: #00958f;
  outline: none;
  box-shadow: 0 0 0 .2rem #00958f40;
}

input[type="submit"] {
  background-color: var(--rcot-color-teal);
  color: var(--rcot-color-white);
  padding: var(--spacing-small) var(--spacing-large);
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 25px;
  width: 100%;
  max-width: 200px;
  margin: auto;
  font-family: Cera Pro, sans-serif;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color .3s, transform .2s;
}

input[type="submit"]:hover {
  background-color: var(--rcot-color-dark-teal);
  transform: translateY(-3px);
}

input[type="submit"]:active {
  transform: translateY(1px);
}

#canvas-container {
  width: 100%;
  padding: var(--spacing-small);
  margin-bottom: var(--spacing-small);
  background-color: var(--rcot-color-white);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

#canvas-container canvas {
  width: 100%;
  max-width: 100%;
  height: auto;
}

.dark-teal-icon {
  color: var(--rcot-color-dark-teal);
  cursor: pointer;
  margin-top: var(--spacing-medium);
  justify-content: center;
  align-items: center;
  font-size: 24px;
  transition: opacity .3s, transform .2s;
  display: flex;
}

.dark-teal-icon.hidden {
  opacity: 0;
  transform: scale(.8);
}

.dark-teal-icon:not(.hidden) {
  opacity: 1;
  transform: scale(1);
}
